@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  width: 100%;
  max-width: 300px;
  padding: 10px;
  margin: 0 auto;
  text-align: center;
}

.input_container {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

input {
  width: 220px;
  height: 25px;
}

.icon_add {
  width: 32px;
  height: 32px;
  margin: 12px;
}

.item_container {
  width: 100%;
  max-width: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  background-color: rgba(199, 199, 199, 0.5);
  margin: 15px;
  /* border: solid red 1px; */
}

.item {
  width: 100%;
  max-width: 400px;
  /* border: solid black 1px; */
  overflow: hidden;
}

span {
  font-family: "Akaya Telivigala", cursive;
  font-size: 2.5rem;
}

.button_container {
  width: 100%;
  max-width: 200px;
  display: inline;
  /* border: solid red 1px; */
  text-align: end;
}

.icon_delete {
  width: 18px;
  height: 18px;
}

.checkmark {
  height: 15px;
  width: 15px;
  background-color: #eee;
  margin: 0 10px 0 0;
}

button {
  margin-left: 10px;
}

.completed {
  text-decoration: line-through;
  color: red;
}

.app_box {
  padding: 10px;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  margin: auto;
}

#app_header {
  color: cadetblue;
  margin-bottom: -10px;
}

.Note_Wrapper {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

